import React from 'react';
import { FaHeart, FaStar, FaSmile, FaGamepad, FaCoffee, FaCode, FaMusic, FaMoon, FaSun, FaLeaf, FaSnowflake, FaUmbrella, FaPlane, FaShip, FaTrain, FaCar, FaBicycle, FaBook, FaCamera, FaPalette } from 'react-icons/fa';
import './trpg.css'; //   

const TrpgStepShow = ({ step, userInfo, content, prompt }) => (
    <div className="trpg-stepShow">
        <div>
            <h3>  :</h3>
            <div>
                <h4> :</h4>
                {/*     */}
                {userInfo.icon && (
                    <p>
                         : {userInfo.icon === 'none' ? '' : getIconComponent(userInfo.icon)}
                    </p>
                )}
                <p> : {userInfo.playerName}</p>
                <p>: {userInfo.introduction}</p>
            </div>
            <div>
                <h4>:</h4>
                <p> : {content.firstMessage}</p>
                <p> :</p>
                <ul>
                    {content.exampleQuestions.map((question, index) => (
                        <li key={index}>{question}</li>
                    ))}
                </ul>
            </div>
            <div>
                <h4>:</h4>
                <p>: {prompt.role}</p>
                <p>: {prompt.personality}</p>
                <p>: {prompt.requirements}</p>
            </div>
        </div>
    </div>
);

const getIconComponent = (icon) => {
    switch (icon) {
        case 'nope':
            return;
        case 'heart':
            return <FaHeart />;
        case 'star':
            return <FaStar />;
        case 'smile':
            return <FaSmile />;
        case 'gamepad':
            return <FaGamepad />;
        case 'coffee':
            return <FaCoffee />;
        case 'code':
            return <FaCode />;
        case 'music':
            return <FaMusic />;
        case 'moon':
            return <FaMoon />;
        case 'sun':
            return <FaSun />;
        case 'leaf':
            return <FaLeaf />;
        case 'snowflake':
            return <FaSnowflake />;
        case 'umbrella':
            return <FaUmbrella />;
        case 'plane':
            return <FaPlane />;
        case 'ship':
            return <FaShip />;
        case 'train':
            return <FaTrain />;
        case 'car':
            return <FaCar />;
        case 'bicycle':
            return <FaBicycle />;
        case 'book':
            return <FaBook />;
        case 'camera':
            return <FaCamera />;
        case 'palette':
            return <FaPalette />;

        default:
            return null;
    }
};

export default TrpgStepShow;